import React, { useState, useRef } from 'react';
import './App.css';
import NavigationBar from './Components/NavigationBar';
import CVSummary from './Components/CV/CVSummary';
import { createMuiTheme, Grid, Hidden, ThemeProvider, Typography } from '@material-ui/core';
import Curibo from './Components/Projects/Curibo';
import NUMaps from './Components/Projects/NUMaps';
import CV from './Components/CV/CV';
import Eden from './Components/Research Projects/Eden';
import Music from './Components/MusicProjects/Music';
import One from './Components/CommunityProjects/ONE';

function App() {

  const [darkMode, setDarkMode] = useState(false);

  const theme = createMuiTheme({
    palette: {
      type: darkMode ? "dark" : "light",
    },
    typography: {
      fontFamily: [
        '"Helvetica Neue"'
      ]
    }
  })

  const toggleDarkMode = () => {
    setDarkMode(!darkMode)
  }

  const scrollToRef = (ref) => window.scroll({ top: ref.current.offsetTop, left: 0, behavior: 'smooth' })

  const aboutRef = useRef(null)
  const researchProjectsRef = useRef(null)
  const designProjectsRef = useRef(null)
  const communityProjectsRef = useRef(null)
  const musicRef = useRef(null)
  const resumeRef = useRef(null)
  const contactRef = useRef(null)

  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <Grid container justify="center">
          <Hidden smDown>
          <Grid item xl={2} lg={3} md={3} xs={3}>
            <NavigationBar 
            darkMode={darkMode} 
            toggleDarkMode={toggleDarkMode} 
            scrollToAbout={() => scrollToRef(aboutRef)}
            scrollToResearchProjects={() => scrollToRef(researchProjectsRef)}
            scrollToDesignProjects={() => scrollToRef(designProjectsRef)}
            scrollToCommunityProjects={() => scrollToRef(communityProjectsRef)}
            scrollToMusic={() => scrollToRef(musicRef)}
            scrollToResume={() => scrollToRef(resumeRef)}
            scrollToContact={() => scrollToRef(contactRef)}
            style={{width: "100%"}}
            />
          </Grid>
          </Hidden>
          <Grid item xl={10} lg={8} md={9} xs={12} style={{maxWidth: 1500}}>
            <Grid container justify="center" spacing={3}>
              {/*  CV Summary */}
              <Grid item xs={11} ref={aboutRef}>
                <CVSummary darkMode={darkMode} />
              </Grid>
              {/*  Research Projects */}
              <Grid item xs={11} ref={researchProjectsRef}>
                <Typography variant="h2" align="left" style={{ fontWeight: 800, opacity: 0.2, marginBottom: 100 }}>Research Projects</Typography>
              </Grid>
              <Eden darkMode={darkMode} />
              {/*  Design Projects */}
              <Grid item xs={11} ref={designProjectsRef}>
                <Typography variant="h2" align="left" style={{ fontWeight: 800, opacity: 0.2, marginBottom: 100, marginTop: 100 }}>Design Projects</Typography>
              </Grid>
              <Curibo />
              <NUMaps />
              <Grid item xs={11} ref={communityProjectsRef}>
                <Typography variant="h2" align="left" style={{ fontWeight: 800, opacity: 0.2, marginBottom: 100, marginTop: 100 }}>Community Projects</Typography>
              </Grid>
              <One />
              <Grid item xs={11} ref={musicRef}>
                <Typography variant="h2" align="left" style={{ fontWeight: 800, opacity: 0.2, marginBottom: 100, marginTop: 100 }}>Music</Typography>
              </Grid>
              <Music />
              <Grid item xs={11} ref={resumeRef}>
                <Typography variant="h2" align="left" style={{ fontWeight: 800, opacity: 0.2, marginBottom: 100, marginTop: 100 }}>Resume</Typography>
              </Grid>
              <CV />
            </Grid>
          </Grid>
        </Grid>
      </ThemeProvider>
    </div>
  );
}

export default App;
